import { handleSignOut } from "src/services/cognito/logout/logout";
import { Button } from "./home.styles"
import { useEffect } from "react";
import { useNavigate } from "react-router-dom";

export const Home = () => {
  const navigate = useNavigate();

  const handleLogout = async () => {
    await handleSignOut();
    localStorage.removeItem('user');
    navigate("/");
  };

  // const getListUsers = async (token: string) => {
  //   const response = await fetch('https://0o7co3sm45.execute-api.us-east-1.amazonaws.com/dev/users', {
  //     method: "GET",
  //     headers: {
  //       Authorization: `Bearer ${token}`
  //     }
  //   });

  //   console.log(response);
  // }

  useEffect(() => {
    const user = localStorage.getItem("user");
    if(!user) {
      handleLogout();
      return;
    }
    const dataUser = JSON.parse(user);
    
    if(!dataUser.isLogged || !dataUser.token){
      handleLogout();
      return;
    }

    // getListUsers(dataUser.token);
    
    // if(isLogged) {
    //   navigate("/");
    // }
  }, []); // Carga solo 1 vez y es al principio de la pgina

  

  return (
    <div>
      <h1>Bienvenido, nombre</h1>
      <Button type="button" onClick={handleLogout}>
        Logout
      </Button>
    </div>
  )
}